import React, { useState, useEffect } from "react";
import Counter from "../counter";
import HtmlMapper from "react-html-map";
import { StaticImage } from "gatsby-plugin-image";
import data22 from "/test";

const Component = ({ children, data, onUpdate }) => {
  const [toggleValue, setToggleValue] = useState(data.num);
  const handleToggle = () => {
    setToggleValue(`${data.num} ${data?.provider || ""}`)
  }
  return (
    <div className="product__item-wrapper mb-20" onClick={handleToggle}>
      <div className="product__item white-bg fs-5 text-center align-middle">
        {toggleValue}
      </div>
    </div>
  );
};

export default Component;
